.container{
    background-color: white;
    margin-bottom: 20px;
}

.background img{
    width: 200px;
    object-fit: cover;
}
.photo img{
    width: 80px;
    height: 80px;
    border-radius: 40px;
    margin: -40px 0 0 65px;
}
.name{
    text-align: center;
}
.container p{
    text-align: center;
}
@media all and (max-width: 1350px) {
    .background img{
        width: 140px;
    }
    .photo img{
        margin: -40px 0 0 35px;
    }
    .container p{
        font-size: 14px;
    }
}